.ImageGallery {
	display: grid;
	max-width: calc(100vw - 48px);
	grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
	grid-gap: 16px;
	margin-top: 20px;
	margin-bottom: 20px;
	padding: 0;
	list-style: none;
	margin-left: auto;
	margin-right: auto;
}
.ImageGalleryItem {
	border-radius: 2px;
	box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}